.work-process {

    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;

    @include mq(699px) {
        flex-direction: column-reverse;
    }

    &__icon-slider {
        width: 50%;
        max-width: 100%;
        align-self: flex-start;
        display: flex;
        justify-content: center;
        position: relative;

        @include mq(699px) {
            margin: 25px auto;
            margin-bottom: 0;
            width: 400px;
            max-width: 100%;
        }

        &--arrow {
            outline: none !important;
            position: absolute;
            top: calc(50% - 20px);
            width: 28px;
            height: 28px;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            border-radius: 50%;
            cursor: pointer;
            transition: all 0.2s ease;

            @include mq(768px) {
                // top: calc(50% - 20px);
            }

            &:hover {
                background: rgba(250, 250, 250, 0.808);
                box-shadow: 0px 5px 6px rgba(0, 0, 0, 0.25);
            }

            &:active {
                transform: translateY(2px);
                box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.25);
            }

            & svg {
                position: absolute;
                left: calc(50% - 2px);
                top: calc(50% - 4px);

            }

            &-prev {
                left: 15%;
                @include mq(768px) {
                    left: 14%;
                }

                @include mq(699px) {
                    left: 11%;
                }

                @include mq(399px) {
                    left: -3%;
                }
            }

            &-next {
                right: 15%;
                @include mq(768px) {
                    right: 14%;
                }

                @include mq(699px) {
                    right: 11%;
                }

                @include mq(399px) {
                    right: -3%;
                }
            }
        }

        &--carousel {
            width: 50%;
            max-width: 100%;
            box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.25);

            @include mq(399px) {
                width: 80%;
            }
        }

        &--item {
            outline: none;

            &>div {
                display: flex;
                flex-direction: column;
                align-items: center;
                padding: 25px 0px;

            }

            & img {

                @include mq(959px) {
                    transform: scale(0.9)
                }

                @include mq(768px) {
                    transform: scale(0.8)
                }
            }

            & h4 {
                margin: 0;
                margin-top: 17px;
                font-size: 18px;
                
                @include mq(959px) {
                    font-size: 17px;
                }

                @include mq(768px) {
                    font-size: 16px;
                }
            }
        }
    }

    &__text-slider {
        width: 50%;

        @include mq(699px) {
            width: 100%;
        }

        & * {
            outline: none;
        }

        &--carousel {
            
        }

        &--item {

            & h3 {
                font-size: 32px;

                @include mq(959px) {
                    font-size: 30px;
                }

                @include mq(768px) {
                    font-size: 28px;
                }
            }

            & p {
                margin-top: 30px;
                font-size: 16px;

                @include mq(959px) {
                    font-size: 15px;
                }

                @include mq(768px) {
                    font-size: 14px;
                }
            }
        }
    }
}