.site-block {

    &__inner {

        &:first-of-type {
            width: calc(100% / 12 * 5 - 20px);

            @include mq(1023px) {
                width: calc(100% / 12 * 5.7 - 20px);
            }

            @include mq(768px) {
                width: 60%;
            }

            @include mq(599px) {
                width: 70%;
            }

            @include mq(499px) {
                width: 100%;
            }
        }

        &:last-of-type {
            width: calc(100% / 12 * 6 - 20px);

            @include mq(768px) {
                margin-top: 20px;
                width: 100%;
            }
        }
    }

    &__card {
        box-sizing: border-box;
        background: $bg-dark;
        color: white;
        width: 100%;
        height: 100%;
        padding: 30px;
        position: relative;

        @include mq(374px) {
            padding: 24px;
        }

        &-title {
            margin: 0;
        }

        &-icon {
            position: absolute;
            right: 30px;
            top: 54px;
        }

        &-price {
            margin-top: 90px;
            padding: 21px 25px;
            background: rgba(0, 0, 0, 0.1);
            display: flex;
            justify-content: space-between;

            @include mq(374px) {
                padding: 18px;
                font-size: 15px;
            }

            span {
                transform: translateY(-2px);
                display: block;
                flex-grow: 1;
                margin: 0px 10px;
                border-bottom: 1.5px dotted white;

                @include mq(959px) {
                    display: none;
                }
                
            }
        }
    }

    &__title {

    }

    &__text {

    }
}