.tech-stack {
    
    &__title {
        padding-bottom: 30px;
    }
    
    &__list {
        display: flex;
        flex-wrap: wrap;

        margin: 0 -10px;
        margin-bottom: -20px;

        @include mq(479px ,max) {
            margin: 0 -8px;
        }

        &:not(:first-of-type) {
            margin-top: 20px;
        }
    }


    &__item {
        margin: 0 10px;
        padding: 20px 10px;
        box-sizing: border-box;
        word-wrap: break-word;
        width: calc(100% / 12 * 2 - 20px);
        box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.25);
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        margin-bottom: 20px;

        @include mq(959px ,max) {
            width: calc(100% / 12 * 4 - 20px);
        }

        @include mq(479px ,max) {
            margin: 0 8px;
            margin-bottom: 16px;
            width: calc(100% / 12 * 6 - 16px);
        }

        &::before {
            display: none;
        }

        &-img {

        }

        &-title {
            margin: 0;
            margin-top: 20px;
            font-weight: bold;
            font-size: 14px;
            text-align: center;
        }
    }
}